.Search-filter .form-control {height: 40px !important; font-size: 13px; color: #000; border: 1px solid #e5eff3; outline: none; box-shadow: none; }
button.search-btn {background: #2196f3; color: #fff; border: none; padding: 6px 15px; font-weight: bold; border-radius: 5px; }
button.search-btn.btn-danger{background: #dc3545; }
button.navbar-toggler {background: #436abd; color: #fff; }
